<template>
  <div class="flex h-full min-h-[500px] w-full flex-col">
    <GeneralForm
      :heading="`Add Products to ${companyName}`"
      :showCloseButton="true"
      @close-pressed="handleClose"
      @form-submitted="handleSubmit"
      :is-form-submitting="isFormSubmitting"
      class="flex-1"
    >
      <template #form-body>
        <ProductSearchDropdown
          :dropdown-label="'Products'"
          :dropdown-id="'products'"
          :products="formData.products"
          :is-multi-select="true"
          :black-listed="blackListed"
          @on-products-update="updateProducts"
          :show-select-button="false"
        />
      </template>
    </GeneralForm>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  props: {
    companyName: {
      type: String,
      required: true,
    },
    companyId: {
      type: String,
      required: true,
    },
    products: {
      type: Array,
      required: false,
    },
  },
  components: {
    GeneralForm: () => import("@/components/General/Form.vue"),
    ProductSearchDropdown: () =>
      import("@/components/Utility/CustomDropdowns/ProductSearch.vue"),
  },
  computed: {
    ...mapGetters({
      user: "auth/getUser",
    }),
    blackListed() {
      return this?.products?.map((item) => item?._id) || [];
    },
  },
  data() {
    return {
      formData: {
        products: [],
      },
      isFormSubmitting: false,
    };
  },
  methods: {
    ...mapActions({
      addProductToCompany: "company/addProductToCompany",
    }),
    async handleSubmit() {
      this.isFormSubmitting = true;
      let payload = {
        id: this.companyId,
        body: this.formData?.products?.map((product) => ({
          productId: product?._id,
        })),
      };
      try {
        const res = await this.addProductToCompany(payload);
        if (res?.success) {
          this.handleClose();
          this.$bus.$emit("refetch-company-details");
        } else {
          console.error("error", res);
        }
      } catch (error) {
        console.error("error", error);
      }
      this.isFormSubmitting = false;
    },

    handleClose() {
      this.$bus.$emit("update-category-to-company-modal", { showModal: false });
    },
    updateProducts(products) {
      this.formData.products = products;
    },
  },
};
</script>
